.home-container {
  width: 100%;
  height: calc(100vh - 55px);
}

.left-section {
  width: 100%;
  height: calc(100vh - 55px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hello-text {
  font-size: 2rem;
}

.sub-description {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  color: var(--text-color);
  opacity: 0.5;
  margin-top: 1rem;
}

.title-description {
  font-size: 2rem;
  color: var(--title-color);
  margin-top: 1rem;
}

.resume-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.play-button {
  width: 75px;
  margin-top: 1rem;
}

.resume-description-text {
  margin-top: 0.75rem;
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
  color: var(--title-color);
  letter-spacing: 0.25rem;
}

.computer-image {
  border-radius: 1rem;
  width: 50%;
  height: 300px;
  margin: 1rem 0;
  object-fit: cover;
}

.contact-detail-title {
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-size: 10px;
  font-weight: 800;
  color: var(--title-color);
  opacity: 0.5;
  margin-bottom: 1rem;
  text-align: center;
}

.email-detail,
.address-detail {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  letter-spacing: 0.25rem;
  font-weight: bold;
  text-align: center;
}

.email,
.address {
  color: var(--text-color);
  letter-spacing: 0;
}

.hero {
  width: 100%;
  height: 100%;
  border-radius: 0;
  margin-top: 0;
}

@media (max-width: 576px) {
  .home-container {
    width: 100%;
    height: auto;
  }
  
  .hello-text {
    font-size: 1rem;
  }

  .sub-description {
    font-size: 0.6rem;
    text-align: center;
  }

  .title-description {
    font-size: 1.5rem;
  }

  .play-button {
    width: 50px;
  }

  .computer-image {
    width: 95%;
    margin: 0.5rem 0;
  }
}
